















































import { Component, Prop, Vue } from "vue-property-decorator";

interface TestDriveFormInterface {
  email: string;
  name: string;
  query: string;
}

@Component
export default class QueryModal extends Vue {
  form: TestDriveFormInterface = {
    email: "",
    name: "",
    query: "",
  };

  show = true;

  onSubmit(evt: Event) {
    evt.preventDefault();
    alert(JSON.stringify(this.form));
  }

  onReset(evt: Event) {
    evt.preventDefault();
    // Reset our form values
    this.form.email = "";
    this.form.name = "";
    this.form.query = "";
    this.show = false;
    this.$nextTick(() => {
      this.show = true;
    });
  }
}
